export const Network = (
  playerId: PlayerId,
  fullQuery: string | undefined,
  receiver: (event: GameEvent, value: unknown) => void
) => {
  // const socket = io('http://127.0.0.1:3939')
  console.log('playerId:', playerId, 'fullQuery:', fullQuery)

  let devChannel = useDevChannel ? new BroadcastChannel('dev_bus') : null

  let rtps: any[] = []
  let connected = false
  // socket.on('connected', () => {
  //   console.log('connected')
  //   rtps.forEach((data) => {
  //     socket.emit('signal', data)
  //   })
  //   connected = true
  // })

  // socket.on('signal', (data: any) => {
  //   console.log('socket: received signal', data)
  //   if (peer) {
  //     peer.signal(data)
  //   }
  // })

  const send = (event: GameEvent, value: unknown) => {
    console.log('===[sent]===>', event, value)

    dataChannel?.send(JSON.stringify({ event, value }))
  }

  const received = (data: string) => {
    console.log('received', data)
    const parsedData = JSON.parse(data)
    receiver(parsedData.event, parsedData.value)
  }

  let allLength: number = 0
  let allQuery: string = ''

  let sendAllQueryTimeout: number | null = null
  const sendAllQuery = () => {
    if (sendAllQueryTimeout) clearTimeout(sendAllQueryTimeout)

    sendAllQueryTimeout = setTimeout(() => {
      if (devChannel && playerId === 2) {
        devChannel.postMessage(allQuery)
      }
      received(
        JSON.stringify({
          event:
            window.location.search.length === 0
              ? 'evPlayer1UrlGenerated'
              : 'evPlayer2PeerParamGenerated',
          value:
            window.location.search.length === 0
              ? window.location.href + '?' + allQuery
              : allQuery,
        })
      )
    }, 1500) as unknown as number
  }

  const peerSignalHandler = (data: any) => {
    const dataStr = JSON.stringify(data)
    const add = true //!dataStr.includes('local') // && !dataStr.includes('158.247.247.252')
    const b64e = btoa(dataStr)
    allLength += b64e.length
    const b64d = atob(b64e)
    const finalData = JSON.parse(b64d)
    // console.log(' >> ', b64e, b64e.length)
    if (add) {
      allQuery += `q=${b64e}&`
      console.log('keep', data)
    }
    // console.log(' >>>> ', atob(b64e), JSON.parse(atob(b64e)))
    console.log('allQuery:', allQuery, b64e.length, allQuery.length)
    const val = JSON.stringify(data) + '\n\n'
    if (!connected) rtps.push(finalData)
    // else socket.emit('signal', finalData)
    sendAllQuery()
  }
  var peer: any
  const updatePeerParams = (param: string) => {
    const queries = new URLSearchParams(param).getAll('q')
    queries.forEach((q) => {
      const parsedData = JSON.parse(atob(q))
      console.log('applying:', parsedData)
      if (playerId === 1) {
        if (parsedData.type === 'answer') {
          ;(peer as RTCPeerConnection).setRemoteDescription(parsedData)
        } else {
          ;(peer as RTCPeerConnection).addIceCandidate(parsedData)
        }
      } else {
        console.error('Must Not be here')
      }
    })
  }

  // only for vanilla [[[
  let dataChannel: RTCDataChannel | null
  function setupDataChannel() {
    checkDataChannelState()
    if (dataChannel) {
      dataChannel.onopen = checkDataChannelState
      dataChannel.onclose = checkDataChannelState
      dataChannel.onmessage = (event) => {
        console.log('<===[received]===', JSON.parse(event.data))
        received(event.data)
      }
    }
  }

  function checkDataChannelState() {
    console.log('WebRTC channel state is changed', dataChannel?.readyState)
    if (dataChannel?.readyState === 'open') {
      received(JSON.stringify({ event: 'evPeerConnected' }))
    }
  }
  // ]]]

  // ------------vanilla peer
  peer = new RTCPeerConnection(config)

  peer.onconnectionstatechange = () => {
    console.log('onconnectionstatechange', peer.connectionState)
  }

  peer.onicegatheringstatechange = () => {
    console.log('Local ICE gathering state:', peer.iceGatheringState)
  }
  ;(peer as RTCPeerConnection).oniceconnectionstatechange = () => {
    console.log(
      'ICE connection state:',
      (peer as RTCPeerConnection).iceConnectionState
    )
  }
  ;(peer as RTCPeerConnection).onicecandidateerror = (event: any) => {
    console.log('onicecandidateerror:', event.errorCode, event.errorText)
  }
  ;(peer as RTCPeerConnection).onicecandidate = (
    event: RTCPeerConnectionIceEvent
  ) => {
    console.log('new candidate:', event.candidate)
    if (event.candidate) {
      peerSignalHandler(event.candidate)
    }
  }
  ;(peer as RTCPeerConnection).onsignalingstatechange = () => {
    console.log(
      'signalingstatechange:',
      (peer as RTCPeerConnection).signalingState
    )
  }

  let initData = null

  if (playerId === 1) {
    ;(peer as RTCPeerConnection).onnegotiationneeded = () => {
      console.log('onnegotiationneeded')
      ;(peer as RTCPeerConnection).createOffer().then((initData) => {
        console.log('offer:', initData)
        peer.setLocalDescription(initData).then(() => {
          console.log('offer setting done')
          console.log((peer as RTCPeerConnection).getConfiguration())
          console.log(peer as RTCPeerConnection)
        })
        peerSignalHandler(initData)
      })
    }

    dataChannel = (peer as RTCPeerConnection).createDataChannel('channel')
    setupDataChannel()

    if (devChannel) {
      devChannel.onmessage = (event) => {
        console.log('from devChannel', event)
        updatePeerParams(event.data)
      }
    }
  } else {
    ;(peer as RTCPeerConnection).ondatachannel = (event) => {
      dataChannel = event.channel
      setupDataChannel()
    }
    if (fullQuery) {
      const queries = new URLSearchParams(fullQuery).getAll('q')

      queries.forEach((q) => {
        const parsedData = JSON.parse(atob(q))
        console.log('applying:', parsedData)
        if (parsedData.type === 'offer') {
          ;(peer as RTCPeerConnection)
            .setRemoteDescription(parsedData)
            .then(() => {
              ;(peer as RTCPeerConnection).createAnswer().then((initData) => {
                // keep 'answer' to pass to offerer
                peerSignalHandler(initData)
                ;(peer as RTCPeerConnection).setLocalDescription(initData)
              })
            })
        } else {
          ;(peer as RTCPeerConnection).addIceCandidate(parsedData)
        }
      })
    }
  }

  return { send, received, updatePeerParams }
}

